<template>
  <div class="phone-slider">
    <flickity ref="flickity" :options="flickityOptions" class="test">
      <div class="carousel-cell cellphone_1">
        <div class="overlay"></div>
      </div>
      <div class="carousel-cell cellphone_2">
        <div class="overlay"></div>
      </div>
      <div class="carousel-cell cellphone_3">
        <div class="overlay"></div>
      </div>
      <div class="carousel-cell cellphone_4">
        <div class="overlay"></div>
      </div>
      <div class="carousel-cell cellphone_5">
        <div class="overlay"></div>
      </div>
    </flickity>
    <button class="slide-button left" @click="previous()"></button>
    <button class="slide-button right" @click="next()"></button>
  </div>
</template>

<script>
import Flickity from 'vue-flickity';

export default {
  name: 'slider',
  components: {
    Flickity,
  },
  data() {
    return {
      flickityOptions: {
        initialIndex: 2,
        prevNextButtons: false,
        pageDots: false,
        wrapAround: false,
      },
    };
  },
  methods: {
    next() {
      this.$refs.flickity.next();
    },
    previous() {
      this.$refs.flickity.previous();
    },
  },
};
</script>
